body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{
  padding: 0 1rem;
}

.content{
  min-height: calc(100vh - 70px - 64px);
  padding: 1.5rem 0;
}

.goods{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card{
  display: flex;
  flex-direction: column;
}

.card-content{
  flex-grow: 1;
}

.Cart{
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  cursor: pointer;
  z-index: 5;
  padding: 3rem;
  

}

@media(min-width: 767px){
  .Cart{
  top: 5rem;
  bottom: unset;
  padding: 3rem;
  }
}

.cardd{
  font-size: 2.5rem;
}

.cart-list{
  position: fixed;
  top: 50%;
  left: 50%; 
  transform: translate(-50%, -50%);
  width: 50rem;
  max-width: 95%;
  border: none;

  animation: show 500ms ease-in-out;
}

.card-list-close{
  cursor: pointer;
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: #fff;
}

.delete-item{
  cursor: pointer;
}

@keyframes show {
  from{
    top: 70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}

.icon-change-quantity{
  cursor: pointer;
  vertical-align: middle;
  color: #26a69a;
}
